import {userList} from "../../APIs/index.js"
export default {
	data() {
		return {
			routineMenus:[]
		};
	},
	onReady() {
		this.loadMore()
	},
	methods:{
		loadMore(){
			userList().then(res=>{
				let [err,{data}] = res;
				if(data.code==1){
					this.routineMenus = data.data
				}
			})
		}
	}
}
